.new-expense__controls {
    display: flex;
 
    flex-wrap: wrap;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: left;
    
  }
  
  .new-expense__control label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
  }
  
  .new-expense__control input {
    padding: 0.5rem;
    border-radius: 0.5rem;
    border: 2px solid;
    width: 10rem;
    max-width: 100%;
  }
  
  .new-expense__control.inValid input{
    border-color:rgb(255, 6, 6);
  }
  
  .new-expense__actions {
    text-align: right;
  }